import React, {Component} from 'react'
import classNames from 'classnames'
// import "./Preferences.scss"
import button from '../../styles/buttons.scss'
import style from '../../styles/Preferences.sass'
import Page from './Page'

import { exportData, importData } from '../../scripts/XMLDataFile'
import { resetHomebook } from '../../scripts/ResetHombook'
import { markAsShown } from '../../scripts/onboarding'
import { forceFileDownload } from '../../tools'
import { DOCTYPE_META } from '../../constants'

import ConfirmModal from '../modals/ConfirmModal'
import confirm from '../../scripts/confirm'

const modal = (
    <ConfirmModal>
        <p>
            tes-vous sr de vouloir rinitialiser votre Homebook ?<br/>
            Toutes donnes sera perdu.
        </p>
    </ConfirmModal>
)

export default class Preferences extends Component { 

    constructor(props, context) {
        super(props, context)

        this.state = {
            importerDrag: false,
            exportLoad: false,
            importLoad: false,
            resetOnboardingLoad: false,
            resetHomebookLoad: false
        }
    }

    resetOnboarding() {
        if(this.state.resetOnboardingLoad) return
        this.setState({resetOnboardingLoad: true})
        markAsShown(false)
        .then(() => {
            this.setState({resetOnboardingLoad: false})
            this.props.history.push('/')
        })
    }

    exportHomebook() {
        if(this.state.exportLoad) return
        this.setState({
            exportLoad: true
        })
        exportData()
        .then(link => forceFileDownload(link, 'homebook.zip'))
        .then(() => {
            this.setState({exportLoad: false})
        })
    }

    importHomebook(file){
        if(this.state.importLoad) return 
        if(!file.name.endsWith('.zip')) return
            
        this.setState({importLoad: true, importerDrag: false})
        console.log('Import', file)
        importData(file)
        .then(() => this.setState({importLoad: false}))
        .catch(err => console.error('Importer:', err))
    }

    dropFile(e) {
        e.stopPropagation()
        e.preventDefault()
        const file = e.dataTransfer.files[0]
        //import file
        this.importHomebook(file)
    }

    resetHomebook() {
        if(this.state.resetHomebookLoad) return
        confirm(modal)
        .then(() => {
            this.setState({resetHomebookLoad: true})
            return resetHomebook()
        })
        .then(() => {
            setTimeout(() => {
                window.location.href = '/'
            }, 2000)
        })
        .catch(err => {})
    }

    render() {
        return (
            <Page title="Prfrences">
                <article>
                    <h2 className={style.title}>Exporter votre HOMEBOOK</h2>
                    <div className={style.article}>
                        <p>
                            Si vous disposez dj dun carnet numrique du logement, vous pouvez lintgrer
                            dans votre homebook par simple importation du fichier de type .zip fourni par loprateur
                            de ce carnet De la mme manire, vous pouvez exporter le carnet numrique homebook.
                            Cette action gnrera un fichier .zip sur votre ordinateur.
                        </p>

                        <button className={classNames(button.button, button.default, {[style.load]: this.state.importLoad})}
                            onDrop={e => this.dropFile(e)}
                            onDragEnter={e => this.setState({importerDrag: true})}
                            onDragLeave={e => this.setState({importerDrag: false})}
                            onDragOver={e => e.preventDefault()}
                            onClick={e => this.refs.importer.click()} disabled={this.state.importLoad}
                            style={this.state.importerDrag ? {backgroundColor: '#39495d'} : {}}>
                            <i className="ion-ios-upload-outline"></i> IMPORTER VOTRE CARNET NUMRIQUE DU LOGEMENT
                            <input ref="importer" type="file" className="hidden" onChange={e => this.importHomebook(e.target.files[0])}/>
                        </button>
                        <button className={classNames(button.button, button.default, {[style.load]: this.state.exportLoad})} onClick={e => this.exportHomebook()} disabled={this.state.exportLoad}>
                            <i className="ion-ios-download-outline"></i> EXPORTERVOTRE CARNET NUMRIQUE DU LOGEMENT
                        </button>
                        
                    </div>
                </article>

                <article>
                    <h2 className={style.title}>Revoir la prsentation de Homebook</h2>
                    <div className={style.article}>
                        <p>
                            A votre premire connexion, nous vous avons prsent les grandes
                            fonctionnalits: vous pouvez les consulter  nouveau avec ce bouton.
                        </p>

                        <button className={classNames(button.button, button.assertive, {[style.load]: this.state.resetOnboardingLoad})}
                            onClick={() => this.resetOnboarding()}
                            disabled={this.state.resetOnboardingLoad}>
                            <i className="ion-ios-refresh-outline"></i>Revoir la prsentation de Homebook
                        </button>
                    </div>
                </article>

                {process.env.NODE_ENV === 'development' && (<article>
                    <h2 className={style.title}>Reset de Homebook</h2>
                    <div className={style.article}>
                        <p>
                            Attention supprime l'intgralit des informations de Homebook
                        </p>

                        <button className={classNames(button.button, button.assertive, {[style.load]: this.state.resetHomebookLoad})}
                            onClick={() => this.resetHomebook()}
                            disabled={this.state.resetHomebookLoad}>
                            <i className="ion-ios-refresh-outline"></i>Reset Homebook
                        </button>
                    </div>
                </article>)}
            </Page>
        )
    }
}



