                            <p>A aucun moment vos donnes ne sortent de votre espace personnel. Vos donnes ne peuvent tre utilises par une autre application de lespace Cozy que sous votre autorisation.<br/><br/>Pour plus dinformation, vous pouvez vous rfrer  la convention dexprimentation que vous avez signe.</p>
